import { Component, AfterContentInit } from '@angular/core';
import { NavController, LoadingController, Loading, AlertController } from 'ionic-angular';

import * as steem from "steem";
import { Store } from '@ngrx/store';
import { ModeState, TalkState } from '../../app/ng/reducers/states';

import * as ModeReducer from '../../app/ng/reducers/mode.reducer';
import * as TalkReducer from '../../app/ng/reducers/talk.reducer';
import * as fromActions from '../../app/ng/actions/actions';
import * as srtFromActions from '../../app/ng/actions/srtActions';
import { TalkInfo, TalkHistoryInfo } from '../../app/ng/models/srtModel';

import { Storage } from '@ionic/storage';

var STORAGE_NAME = 'STORAGE_NAME';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})



export class HomePage implements AfterContentInit {

  youId: string;
  friendId: string;

  modes$ = null;
  loader: Loading;

  
  histories: TalkHistoryInfo[];

  constructor(public navCtrl: NavController,
    private modeStore: Store<ModeState>,
    private talkStore: Store<TalkState>,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    private storage: Storage) {

      this.modes$ = modeStore.select(ModeReducer.getMode);
    // this.talks$ = talkStore.select(TalkReducer.getTalk);
  }

  ngAfterContentInit() {
    this.storage.get(STORAGE_NAME).then((val) => {

      if(val == null) {
        this.storage.set(STORAGE_NAME, []);
        this.histories = [];
      } else {
        this.histories = val;
      }
      // console.log('histories', val);
    })
  }


  search() {

    console.log(this.youId);
    console.log(this.friendId);

    if (this.checkNull(this.youId) || this.checkNull(this.friendId)) {
      this.showAlert('당신 혹은 친구의 이름을 입력해주세요');
      return;
    }

    this.getYouInfo(this.youId);
  }

  searchHistory(his: TalkHistoryInfo) {
    this.youId = his.youId;
    this.friendId = his.friendId;

    this.getYouInfo(this.youId);
  }

  countHistory(youId: string, friendId: string) {
    let check: boolean = false;
    for(let ii = 0 ; ii < this.histories.length; ++ii) {
      if(this.histories[ii].youId == youId && this.histories[ii].friendId == friendId) {
        this.histories[ii].count += 1;
        check = true;
      }
    }

    if(check == false) {
      this.histories.push({
        youId: youId,
        friendId: friendId,
        count:1
      });
    }

    this.histories.sort((a: TalkHistoryInfo, b: TalkHistoryInfo) => {

      if ( a.count > b.count ) {
        return -1;
      } else {
        return 1;
      }
    });

    this.storage.set(STORAGE_NAME, this.histories);
  }

  checkNull(str) {
    if (str === undefined
      || str === null
      || str.length <= 0
      || str == '') {
      return true;
    }
    return false;
  }

  showAlert(msg: string) {
    let alert = this.alertCtrl.create({
      title: '경고!',
      subTitle: msg,
      buttons: ['응']
    });
    alert.present();
  }

  getYouInfo(youId: string) {

    this.loader = this.loadingCtrl.create({
      content: "Please wait...",
      // duration: 3000
    });

    this.loader.present();

    this.countHistory(this.youId, this.friendId);

    const _this = this;
    steem.api.getAccountHistory(youId,-1,10000,(err, result) => {

      if (err != null) {
        alert('에러났어요');
        return;
      }

      _this.parseTalk(result);

    });
  }

  parseTalk(result) {


    const list = result;

    const realList = new Array();
    const talkList: Array<any> = new Array();
    for(let i = 0 ; i < list.length; ++i) {
        const data = list[i];

        // console.log(data[0]);
        
        if(data[1].op[0] == undefined || data[1].op[0] == null || data[1].op[0] != 'comment') {
            continue;
        }

        const cmtData = data[1].op[1];

        if(data[1].op[1].parent_author == undefined
             || data[1].op[1].parent_author == null
             || data[1].op[1].parent_author == ''
            ) {
            continue;
        }

        
        if(cmtData.parent_author == this.friendId || cmtData.author == this.friendId) {

            // ddd += cmtData.author;
            // ddd += '\r\n';
            // ddd += cmtData.body;
            // ddd += '\r\n';
            // json_metadata
            data[1].op[1].json_metadata = JSON.parse(data[1].op[1].json_metadata);
            talkList.push(data);
            
        }
        
    }

    // this.viewTalkList = talkList;

    const ti: TalkInfo = {
      talkList : talkList,
      youId : this.youId,
      friendId : this.friendId
    }

    this.loader.dismissAll();

    this.talkStore.dispatch(new srtFromActions.TalkAction(ti));

    this.modeStore.dispatch(new fromActions.ModeAction('talk'));

    

  }

}



