import { Component, AfterContentInit } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Store } from '@ngrx/store';
import { TalkState } from '../../app/ng/reducers/states';

import * as TalkReducer from '../../app/ng/reducers/talk.reducer';
import { Observable } from 'rxjs/Observable';
import { TalkInfo } from '../../app/ng/models/srtModel';

import {Clipboard} from 'ts-clipboard';

import * as StringUtil from '../../app/commons/StringUtil';


@Component({
  selector: 'page-list',
  templateUrl: 'list.html'
})
export class ListPage implements AfterContentInit {
 
  talks$: Observable<TalkInfo>;

  talkInfos = [];

  constructor(public navCtrl: NavController, private talkStore: Store<TalkState>) {
   
    this.talks$ = talkStore.select(TalkReducer.getTalk);
  }

  ngAfterContentInit() {

    this.talks$.subscribe(
      (talkInfo: TalkInfo) => {
        this.talkInfos.push(talkInfo);
        console.log(talkInfo.talkList[0]);
      }
    );
  }

  ccopy() {
    

    let copyData = '';

    for (const talkInfo of this.talkInfos) {
      for(const it of talkInfo.talkList) {

        copyData += it[1].op[1].author;
        copyData += ' - ';
        copyData += '<a href="https://steemit.com/' + it[1].op[1].json_metadata.tags[0] + '/@' + it[1].op[1].parent_author + '/' + it[1].op[1].parent_permlink + '"' + ' target="_blank">' +it[1].timestamp + '</a>';
        copyData += '<br/>';
        copyData += it[1].op[1].body;
        copyData += '<br/>';
      }
    }

    // Clipboard.copy(copyData);

    this.download(copyData);

  }

  download(data) {

    const talk: TalkInfo = this.talkInfos[0];

    let fileName = '';
    fileName += talk.youId;
    fileName += '-';
    fileName += talk.friendId;

    fileName += '_'
    let start = StringUtil.convertDate((talk.talkList[0])[1].timestamp);
    let end = StringUtil.convertDate((talk.talkList[talk.talkList.length-1])[1].timestamp);

    fileName += start;
    fileName += '-';
    fileName += end;
    fileName += '.html';

    var csvData = new Blob([data], { type: 'text/html;charset=utf-8;' });
    var csvURL = null;
    if (navigator.msSaveBlob) {
      csvURL = navigator.msSaveBlob(csvData, fileName);
    } else {
      csvURL = window.URL.createObjectURL(csvData);
    }
    var tempLink = document.createElement('a');
    tempLink.href = csvURL;
    tempLink.setAttribute('download', fileName);
    tempLink.click();
    tempLink.remove();
  }

  

}




